.modal-container {
    -ms-flex-align: center;
        align-items: center;
    display: -ms-flexbox;
    display: flex;
    height: 100%;
    -ms-flex-pack: center;
        justify-content: center;
    position: fixed;
    right: 0;
    top: 0;
    width: 100%;
}

.modal-overlay {
    background: rgba(0, 0, 0, 0.4);
    height: 100%;
    left: 0;
    position: fixed;
    top: 0;
    width: 100%;
}

.modal-menu {
  background: white;
  border-radius: 12px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  list-style: none;
  max-width: 240px;
  padding: 0;
  width: 100%;
  z-index: 1;
}

.modal-menu li {
    color: gray;
    padding: 12px;
    border-bottom: 1px solid #ddd;
}

.modal-menu li.button {
    -ms-flex-align: center;
        align-items: center;
    display: -ms-flexbox;
    display: flex;
}

.modal-menu li span,
.modal-menu li.button {
    cursor: pointer;
}

.modal-menu li.button:hover,
.modal-menu li span:hover {
    color: #039BE5;
}

.modal-menu li.button i {
    margin-right: 6px;
}

.modal-menu li:last-of-type {
    border-color: transparent;
}

.modal-menu li.heading {
    -ms-flex-align: center;
        align-items: center;
    display: -ms-flexbox;
    display: flex;
    font-size: 150%;
    font-weight: bold;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.modal-menu.create li:last-of-type {
  display: -ms-flexbox;
  display: flex;
  border-bottom: 0;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.custom-die {
    -ms-flex-align: center;
        align-items: center;
    background: #eee;
    border: 1px solid transparent;
    border-radius: 3px;
    display: -ms-flexbox;
    display: flex;
    position: relative;
    -webkit-transition: all 150ms ease-in-out;
    -o-transition: all 150ms ease-in-out;
    transition: all 150ms ease-in-out;
}

.custom-die input {
    background: transparent;
    border: 0;
    font-size: 15px;
    padding: 6px;
    width: 100%;
}

.custom-die input:focus {
    outline: none;
}

.custom-die .create {
    padding: 0 6px;
}

.custom-die .create:hover {
    color: #039BE5;
}

.custom-die.error
, .custom-die .error-message
{
    border: 1px solid #FB8C00;
    border-bottom-left-radius: 0;
}

.custom-die .error-message {
    -ms-flex-align: center;
        align-items: center;
    -webkit-animation: pop-in 150ms ease-in-out;
            animation: pop-in 150ms ease-in-out;
    background: #FFE0B2;
    -webkit-box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2);
            box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2);
    color: #F57C00;
    display: -ms-flexbox;
    display: flex;
    font-size: 12px;
    left: -1px;
    padding: 6px;
    position: absolute;
    top: 100%;
}

.custom-die .error-message i {
    /* color: #FB8C00; */
    margin-right: 3px;
}

.emoji.li {
	margin-right: 6px;
}


