@-webkit-keyframes pop-in {
    from { -webkit-transform: scale(0.8); transform: scale(0.8) }
    to { -webkit-transform: scale(1.0); transform: scale(1.0) }
}

@keyframes pop-in {
    from { -webkit-transform: scale(0.8); transform: scale(0.8) }
    to { -webkit-transform: scale(1.0); transform: scale(1.0) }
}

@-webkit-keyframes pop-out {
    from { -webkit-transform: scale(1.0); transform: scale(1.0) }
    to { -webkit-transform: scale(0.8); transform: scale(0.8) }
}

@keyframes pop-out {
    from { -webkit-transform: scale(1.0); transform: scale(1.0) }
    to { -webkit-transform: scale(0.8); transform: scale(0.8) }
}


